.card{
   width: 400px;
   height: 200px;
   border-radius: 20px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   border:2px solid rgb(38, 13, 231);
}

.card_header{
        width: 100%;
        height: 20%;
        text-align: center;
        color:red;
        font-family: algerian;
        font-size: 2rem;
}

.card_body{
 width: 100%;
 height: 80%;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}